import { Box, Typography, Link } from '@mui/material';
import { memo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {
  userApprovalsSelector,
  userEmailSelector,
  userFriendsSelector,
  userIdSelector,
  userInvitesSelector,
  userIsAuthorizatedSelector,
  userUsersSelector,
} from '../../../modules/user/store/selectors';
import { setLogout } from '../../../modules/user/store/user';
import { socket } from '../../containers/app-container/app-container';
import { axiosInstance } from '../../services/axios-instance';
import { getLogout } from '../../services/get-logout';
import { theme } from '../../theme';
import { Button } from '../button';
import { User } from '../user';

const AllUsers = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const users = useSelector(userUsersSelector);
  const friends = useSelector(userFriendsSelector);
  const invites = useSelector(userInvitesSelector);
  const approvals = useSelector(userApprovalsSelector);
  const isAuthorizated = useSelector(userIsAuthorizatedSelector);
  const userEmail = useSelector(userEmailSelector);
  const userId = useSelector(userIdSelector);

  return (
    <Box sx={{ display: 'grid', rowGap: '16px', position: 'absolute', top: '0', left: '0', padding: '16px' }}>
      {/*   */}
      <Typography variant="h5"> :</Typography>
      {users?.map((user) => {
        return (
          <Box
            key={user._id}
            sx={{
              display: 'inline-grid',
              gridAutoFlow: 'column',
              alignItems: 'center',
              columnGap: '16px',
              backgroundColor: theme.palette.common.white,
              borderRadius: '8px',
              border: `1px solid ${theme.palette.grey[300]}`,
              padding: '8px 16px',
            }}
          >
            <User name={user.name} status={user.status} email={user.email} />
            <Box sx={{ display: 'grid', gridAutoFlow: 'column', columnGap: '16px' }}>
              <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/add-invite-to-friends', { friendId: user._id });

                  socket.emit('on-add-invite-to-friends', user._id);

                  return response.data;
                }}
              >
                  
              </Button>
              {/* <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/add-to-friends', { friendId: user._id });

                  return response.data;
                }}
              >
                
              </Button> */}
              {/* <Button variant="contained" onClick={() => null}>
                
              </Button> */}
            </Box>
          </Box>
        );
      })}
      {/*   */}
      <Typography variant="h5"> :</Typography>
      {friends?.map((friend) => {
        return (
          <Box
            key={friend._id}
            sx={{
              display: 'inline-grid',
              gridAutoFlow: 'column',
              alignItems: 'center',
              columnGap: '16px',
              backgroundColor: theme.palette.common.white,
              borderRadius: '8px',
              border: `1px solid ${theme.palette.grey[300]}`,
              padding: '8px 16px',
            }}
          >
            <User name={friend.name} status={friend.status} email={friend.email} />
            <Box sx={{ display: 'grid', gridAutoFlow: 'column', columnGap: '16px' }}>
              <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/remove-from-friends', { friendId: friend._id });

                  socket.emit('on-remove-from-friends', friend._id); //   ,    

                  return response.data;
                }}
              >
                
              </Button>
            </Box>
          </Box>
        );
      })}
      {/*    */}
      <Typography variant="h5">  :</Typography>
      {invites?.map((invite) => {
        return (
          <Box
            key={invite._id}
            sx={{
              display: 'inline-grid',
              gridAutoFlow: 'column',
              alignItems: 'center',
              columnGap: '16px',
              backgroundColor: theme.palette.common.white,
              borderRadius: '8px',
              border: `1px solid ${theme.palette.grey[300]}`,
              padding: '8px 16px',
            }}
          >
            <User name={invite.name} status={invite.status} email={invite.email} />
            <Box sx={{ display: 'grid', gridAutoFlow: 'column', columnGap: '16px' }}>
              <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/add-to-friends', { friendId: invite._id });

                  socket.emit('on-add-to-friends', invite._id); //   ,    

                  return response.data;
                }}
              >
                
              </Button>
              <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/remove-invite-to-friends', { friendId: invite._id });

                  socket.emit('on-remove-invite-to-friends', invite._id); //   ,      

                  return response.data;

                  // TODO:   
                }}
              >
                
              </Button>
            </Box>
          </Box>
        );
      })}
      {/*     */}
      <Typography variant="h5">   :</Typography>
      {approvals?.map((approval) => {
        return (
          <Box
            key={approval._id}
            sx={{
              display: 'inline-grid',
              gridAutoFlow: 'column',
              alignItems: 'center',
              columnGap: '16px',
              backgroundColor: theme.palette.common.white,
              borderRadius: '8px',
              border: `1px solid ${theme.palette.grey[300]}`,
              padding: '8px 16px',
            }}
          >
            <User name={approval.name} status={approval.status} email={approval.email} />
            <Box sx={{ display: 'grid', gridAutoFlow: 'column', alignItems: 'center', columnGap: '16px' }}>
              <Typography> </Typography>
              <Button
                variant="contained"
                onClick={async () => {
                  const response = await axiosInstance.post('/remove-invite-to-friends', { friendId: approval._id });

                  socket.emit('on-remove-invite-to-friends', approval._id); //   ,        

                  return response.data;

                  // TODO:   
                }}
              >
                
              </Button>
            </Box>
          </Box>
        );
      })}
      {/*    */}
      <Box sx={{ display: 'grid', rowGap: '8px' }}>
        <Typography variant="h5">
          {isAuthorizated ? ` : ${userEmail}.` : '  .'}
        </Typography>
        <Typography variant="h5">
          <Link sx={{ cursor: 'pointer' }} underline="hover" onClick={() => navigate('/profile')}>
             
          </Link>
        </Typography>
        <Typography variant="h5">
          <Link sx={{ cursor: 'pointer' }} underline="hover" onClick={() => navigate('/channels')}>
            
          </Link>
        </Typography>
        <Button
          variant="contained"
          color="primary"
          onClick={() => {
            getLogout();
            socket.emit('on-disconnect', userId);
            dispatch(setLogout());
          }}
        >
            
        </Button>
      </Box>
    </Box>
  );
};

export const AllUsersMemoized = memo(AllUsers);
